import { FlagColor, makeFlag } from "./FlagColors";

export enum OrgSkillLevel {
  estica = 0, // Only Fraternitas Estica
  fraternities1 = 1, // ES, Vironia, Sakala, Ugala, Rotalia
  sororities = 2, // ENS, FP, Indla, Lembela, Amicitia, Sor.Estoniae
  fraternities2 = 3, // Liviensis, Revelia, Tehnola, Leola, Tartuensis
  unions = 4, // Seltsid: Phjala, Liivika, Veljesto, Raimla, Fennica + Arminia
  friends = 5, // Sprusorgid: Lettonia, Polonia, NeoLithuania, Savo, Vrmlands Nation
  latvia = 6,
  level7 = 7,
  level8 = 8,
  level9 = 9,
  level10 = 10,
}

export interface Organization {
  name: string;
  colors: FlagColor[];
  establishedYear: number;
  establishedPlace: string;
  slogan?: string;
  level: OrgSkillLevel;
}

